@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  margin: 0;
}
html {
  font-family: "Noto Sans JP", sans-serif;
  font-size: 100%;
  box-sizing: border-box;
}

body {
  padding: 0px;
  margin: 0px;
  line-height: 1.3;
}

.ovenTempSection {
  display: flex;
  justify-content: space-between;

  margin: 5px;
  padding-bottom: 20px;
}
.ovenTempSection .ovenTemp-container {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  flex-shrink: 4;
}
.ovenTempSection .ovenTempQPick-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  min-height: 210px;
  padding-left: 25px;
}

.buttonsQuick {
  text-align: center;
}

.ovenTimeSection {
  display: flex;
  justify-content: space-between;

  margin: 5px;
}

.ovenTimeSection .ovenTime-container {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  align-content: center;
  flex-wrap: wrap;
  flex-shrink: 12;
}

#ovenTimeNumber {
  font-size: 1.2em;
  width: 50px;
  margin: 0 10px;
  border: outset yellow 3px;
}

.ovenTimeQPick-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  min-height: 210px;
  padding-left: 25px;
}

.headingSection-container {
  display: flex;
  flex-direction: column;

  margin: 50px;
}

.mainSection-container {
  display: flex;
  justify-content: center;
  flex-wrap: wrap-reverse;
}

#ovenSubTitle,
#airFryerSubTitle {
  border-bottom: 2px black solid;
  padding-bottom: 20px;
}

.mainSection-container .ovenSection-container {
  display: flex;
  flex-direction: column;

  margin: 40px;
  padding: 25px;

  border: rgb(206, 56, 19) 10px solid;
  border-radius: 10%;
  border-style: inset;
}

.mainSection-container .airFSection-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  padding: 25px;
  margin: auto 50px;

  border: rgb(206, 56, 19) 10px solid;
  border-style: outset;
  border-radius: 10%;
}

/* 500 Pixels & Smaller Screens  */
@media (max-width: 500px) {
  #title {
    font-size: 40px;
  }
  #titleSub {
    font-size: 20px;
  }
  .headingSection-container {
    margin: 25px;
  }
  #ovenSubTitle {
    font-size: 40px;
  }
  .mainSection-container .airFSection-container {
    margin: auto;
  }
}

